import { createRouter, createWebHistory } from 'vue-router'

let AbouutWe = ()=> import(`@/components/AbiutWe.vue`);



const routes = [
  {
    path: `/aboutWe`,
    name: `about`,
    component: AbouutWe
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to,) {
    if (to.hash) {
      return {
        el: to.hash,
        behavior: 'smooth',
      }
    }
  }
})

export default router
